import React, { createContext, useContext, useEffect, useState } from "react";
import { getCurrentUser, logout as apiLogout } from "@/lib/api";
import { authEvents } from "@/lib/authEvents";

type User = {
  id: string;
  username: string;
  role: "owner" | "staff";
  email?: string;
};

type ContextValue = {
  user: User | null;
  loading: boolean;
  refresh: () => Promise<boolean>;
  logout: () => void;
};

const UserContext = createContext<ContextValue | undefined>(undefined);

export const UserProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  const fetchUser = async (): Promise<boolean> => {
    setLoading(true);
    try {
      const u = await getCurrentUser();
      if (u) {
        setUser({
          id: String(u.id),
          username: String(u.username),
          role: u.role === "owner" ? "owner" : "staff",
          email: u.email,
        });
        return true;
      } else {
        setUser(null);
        return false;
      }
    } catch {
      setUser(null);
      return false;
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUser();
    // subscribe to auth events so we react centrally
    const onLogin = () => void fetchUser();
    const onRefresh = () => void fetchUser();
    const onLogout = () => {
      setUser(null);
      try {
        if (typeof window !== "undefined") window.location.href = "/";
      } catch {
        /* ignore */
      }
    };
    authEvents.on("login", onLogin);
    authEvents.on("refresh", onRefresh);
    authEvents.on("logout", onLogout);
    return () => {
      authEvents.off("login", onLogin);
      authEvents.off("refresh", onRefresh);
      authEvents.off("logout", onLogout);
    };
  }, []);

  const handleLogout = () => {
    apiLogout();
    setUser(null);
    try {
      if (typeof window !== "undefined") window.location.href = "/";
    } catch {
      /* ignore */
    }
  };

  return (
    <UserContext.Provider
      value={{ user, loading, refresh: fetchUser, logout: handleLogout }}
    >
      {children}
    </UserContext.Provider>
  );
};

export function useUser() {
  const ctx = useContext(UserContext);
  if (!ctx) throw new Error("useUser must be used within UserProvider");
  return ctx;
}
