export async function getDailyLogs(filters?: {
  date?: string;
  start?: string;
  end?: string;
  houseId?: number | string;
}) {
  const qs = new URLSearchParams();
  if (filters) {
    if (filters.date) qs.set("date", String(filters.date));
    if (filters.start) qs.set("start", String(filters.start));
    if (filters.end) qs.set("end", String(filters.end));
    if (filters.houseId !== undefined && filters.houseId !== null)
      qs.set("houseId", String(filters.houseId));
  }
  const url = `${BASE}/api/daily-logs${
    qs.toString() ? "?" + qs.toString() : ""
  }`;
  const res = await fetchWithAuth(url);
  const data = await handleResponse(res);
  return data?.data || [];
}

export async function getDailyLogById(id: string) {
  const res = await fetchWithAuth(
    `${BASE}/api/daily-logs/${encodeURIComponent(id)}`
  );
  const data = await handleResponse(res);
  return data?.data || null;
}

