// Convenience: fetch basic feed overview (total stock estimate and monthly cost) on the client
export async function getFeedOverview() {
  try {
    const batches = await getFeedBatches();
    // Sum ingredient amounts across batches (best-effort estimate)
    let totalKg = 0;
    for (const b of batches) {
      if (!b) continue;
      // treat batch as a record and check common size keys
      const rec = b as Record<string, unknown>;
      const candidate =
        rec.size ?? rec.batchSizeKg ?? rec.sizeKg ?? rec.batchSize ?? 0;
      const n = Number(candidate as number | string);
      if (!Number.isNaN(n) && n > 0) totalKg += n;
    }
    // Try to get cost summary for the current month (fallback to null)
    const now = new Date();
    const start = new Date(now.getFullYear(), now.getMonth(), 1)
      .toISOString()
      .slice(0, 10);
    const end = new Date(now.getFullYear(), now.getMonth() + 1, 0)
      .toISOString()
      .slice(0, 10);
    let monthlyCost = null;
    try {
      const cs = await getCostSummary(start, end);
      monthlyCost = cs || null;
    } catch {
      monthlyCost = null;
    }

    return { totalKg, monthlyCost };
  } catch {
    return { totalKg: 0, monthlyCost: null };
  }
}

