"use client";

import React, { useState } from "react";
import { UserProvider, useUser } from "@/context/UserContext";
import { login as apiLogin } from "@/lib/api";
import { DailyLogs } from "@/components/daily-logs";
import { DashboardOverview } from "@/components/dashboard-overview";
import { DailyEntryForm } from "@/components/daily-entry-form";
import { Button } from "@/components/ui/button";

function LoginShell({
  onAuthSuccess,
}: {
  onAuthSuccess: (role: "owner" | "staff") => void;
}) {
  const [loginForm, setLoginForm] = useState({ username: "", password: "" });
  const [loading, setLoading] = useState(false);

  const handle = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      const res = await apiLogin(loginForm.username, loginForm.password);
      if (res && res.success && res.data && res.data.user) {
        const role = (res.data.user.role as string) || "staff";
        onAuthSuccess(role === "owner" ? "owner" : "staff");
      } else {
        alert((res && (res.message || res.error)) || "Login failed");
      }
    } catch {
      alert("Login error");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center">
      <form onSubmit={handle} className="w-full max-w-md p-4">
        <div className="space-y-4">
          <input
            placeholder="username"
            value={loginForm.username}
            onChange={(e) =>
              setLoginForm({ ...loginForm, username: e.target.value })
            }
            className="w-full"
          />
          <input
            type="password"
            placeholder="password"
            value={loginForm.password}
            onChange={(e) =>
              setLoginForm({ ...loginForm, password: e.target.value })
            }
            className="w-full"
          />
          <Button type="submit" className="w-full" disabled={loading}>
            {loading ? "Signing inâ€¦" : "Sign in"}
          </Button>
        </div>
      </form>
    </div>
  );
}

function AuthenticatedApp() {
  const [tab, setTab] = useState<"dashboard" | "daily-entry" | "daily-logs">(
    "dashboard"
  );

  return (
    <div className="min-h-screen p-6">
      <header className="mb-4 flex items-center justify-between">
        <h1 className="text-2xl font-bold">Farm Pilot</h1>
        <div className="space-x-2">
          <Button onClick={() => setTab("dashboard")}>Dashboard</Button>
          <Button onClick={() => setTab("daily-entry")}>Daily Entry</Button>
          <Button onClick={() => setTab("daily-logs")}>Daily Logs</Button>
        </div>
      </header>

      <main>
        {tab === "dashboard" && <DashboardOverview />}
        {tab === "daily-entry" && <DailyEntryForm />}
        {tab === "daily-logs" && <DailyLogs />}
      </main>
    </div>
  );
}

export default function Page() {
  return (
    <UserProvider>
      <AuthGate />
    </UserProvider>
  );
}

function AuthGate() {
  const { user, loading, refresh } = useUser();

  if (loading)
    return (
      <div className="min-h-screen flex items-center justify-center">
        Loadingâ€¦
      </div>
    );

  if (!user)
    return (
      <LoginShell
        onAuthSuccess={async () => {
          await refresh();
        }}
      />
    );

  return <AuthenticatedApp />;
}
